.Form {
    width: 60%;
    margin-left: 15px;
}

.counter-section {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.counter-section .number {
    display: flex;
    width: 25px;
    height: 25px;
    border: 4px solid rgba(221, 221, 221, 0.6);
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    font-size: 1.8rem;
    margin-bottom: 10px;
}

.Form span.number p {
    color: rgba(221, 221, 221, 0.6);
}

span.number.active {
    width: 25px;
    height: 25px;
    display: flex;
    border: none;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    background-color: var(--orange);
}

span.number.active p {
    color: var(--white);
}

span.number.check {
    border: none;
    background-color: var(--orange);
}

.title {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 15px;
}

.title h2 {
    font-size: 3.4rem;
    color: var(--white);
}

.form-section {
    display: grid;
    width: 100vw;
    grid-template-columns: repeat(2, 2fr);
    grid-auto-rows: auto;
}

figure img {
    width: 90%;
    max-width: 550px;
    animation: bounce 6s infinite;
}

figure button.btn-form {
    position: relative;
    right: 60px;
}

.block {
    display: inline-block;
    width: 15px;
    height: 18px;
    background-image: url(../../../assets/images/block.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    right: 32px;
    top: 2px;
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
       transform: translateY(0);
      }
        40% {
         transform: translateY(-30px);
      }
        60% {
         transform: translateY(-15px);
      }
}